import { customElement, property } from 'lit/decorators.js';
import {
  css,
  CSSResultGroup,
  html,
  LitElement,
  TemplateResult,
  unsafeCSS,
} from 'lit';
import pictograms from '@zds/assets/css/main.min.css';

import { PICTOGRAMS } from '../../utils/pictograms';

@customElement('zwc-pictogram')
export class ZwcPictogram extends LitElement {
  static override styles?: CSSResultGroup | undefined = [
    unsafeCSS(pictograms),
    css`
      :host {
        display: inline-flex;
        justify-content: center;
        align-items: center;
      }
    `,
  ];

  @property({ type: Boolean })
  dark = false;

  @property({ type: String })
  pictogram = 'career-analysis';

  get pictogramClass(): string {
    if (!(this.pictogram in PICTOGRAMS)) {
      console.error(`This pictogram does not exist ${this.pictogram}`);
      console.log('Please select from following', Object.keys(PICTOGRAMS));
      return '';
    }

    return `pictogram--${PICTOGRAMS[this.pictogram]['class']}${
      this.dark ? '-dark' : ''
    }`;
  }

  protected render(): TemplateResult {
    return html`<span class="pictogram ${this.pictogramClass}"></span>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-pictogram': ZwcPictogram;
  }
}
